.menu-container {
  min-width: 200px;
  border: solid 1px rgb(85, 2, 2);
  border-radius: 25px;
  background: white;
  display: inline-block;
  margin: 15px;
  width: 250px;
}
h2 {
  text-transform: uppercase;
  color: rgb(85, 2, 2);
  font-size: 1rem;
  min-width: 200px;
  margin: 7px;
}
h3 {
  font-weight: 600;
  margin-bottom: 15px;
}
img {
  max-width: 175px;
  border-radius: 40px;
  opacity: 0.8;
  transition: 0.2s;
}
img:hover {
  opacity: 1;
  cursor: pointer;
  transform: scale(1.45);
}
p {
  padding: 5px;
  font-size: 0.8rem;
  line-height: 1rem;
  letter-spacing: 0.5px;
}
p:hover {
  background: rgb(250, 226, 205);
  border-radius: 25px;
  cursor: pointer;
}
