* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: rgb(243, 242, 242);
  font-family: Georgia, "Times New Roman", Times, serif;
  text-align: center;
}
h1 {
  color: rgb(240, 87, 17);
  font-size: 2rem;
  letter-spacing: 2px;
  margin: 30px 0 15px;
}
input {
  border: 1px solid gray;
  border-radius: 15px;
  background: white;
  padding: 15px;
  margin: 15px;
  min-width: 220px;
}
input::placeholder {
  font-size: 0.7rem;
  font-style: italic;
  text-align: center;
}
.meals-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
